@import '../../Theme/Colors';

.property {   
    min-width: 60px;
    display: flex;

    > label {
        display: inline-block;
        width: $labelwidth;
        vertical-align: top;       
        padding: 3px;
        color: $label-color;

        &.readonly {
            color: #808080;
        }
    }

    &.wide {
        > label {
            width: $labelwidth-wide; 
            overflow: hidden; 
            text-overflow: ellipsis;         
        }
    }

    .labelFullWidth {           
        width: $labelwidth-wide;       
    }

    section {
        min-width: 40px;
        width: calc( 100% - #{$labelwidth} );

        &:global(.full) {
            width: 100%;
            max-width: 100%;
        }
    }

    &.disabled {
        * {
            color: gray;
        }
    }

    .ordered {
        display: inline-block;
    }

    .dynamic {       
        cursor: pointer;
        display: inline-block;
        &:hover {
            :global(.v-icon) {
                color: $info-color;
            }
        }
        :global(.v-icon) {
            font-size: 20px;
        }        
    }
}

:global(.cols_) .property .labelFullWidthDynamic {               
    width: ( calc(#{$labelwidth-wide} + 20px)) !important;  //The 20px is taken from the font size on line 60
}


