@import '../../Theme/Colors';

.dialog {
    .error,
    .updated {
        padding: 5px;
        border: $element-border;
        border-radius: 3px;

        &:empty {
            max-height: 0px;
        }
        max-height: 800px;
        transition: max-height 1s ease-in-out;
        overflow: hidden;
        margin-bottom: 5px;
    }

    .headerError {
        color: $error-color;
    }

    .headerChanges {
        color: $info-color;
    }

    .headerDifferences {
        color: $warning-color;
    }

    .error {
        border-color: $error-border-color;
        background: $error-background;
        color: $error-color;
    }

    .updated {
        border-color: $info-border-color;
        background: $info-background;
        color: $info-color;
    }

    .commands {
        .active {
            font-weight: bold;
        }
        text-align: right;
    }

    section {
        form {
            > ul {
                margin-top: 2px;
                padding: 0px;
                list-style: none;
                > li {
                    margin: 0px;
                }
            }
        }
    }
}
