:global(.status).frame {
    width: 200px;
}

.frame {
    white-space: nowrap;
    padding: 1px 10px;
    ul {
        list-style: none;
        padding: 5px;
        li {
            display: flex;
            label {
                display: inline-block;
                width: 100px;
            }
        }
    }
}