@use '../../Theme/Colors' as colours;
@use '../../Theme/Utilities' as utils;

.status {
    $status-details-color: #202020;

    :global(.v-icon){
        transition: none;
    }

    &.visible {
        background: #343434;
        border-radius: 5px;

        .details {
            visibility: visible;
        }
    }

    @include utils.attention;
    .icon {
        margin-left: 2px;
        cursor: pointer;
        position: relative;
        display: inline-block;

        :global(.v-icon){
            font-size: 1.2em;
            color: inherit;
        }

        &, :global(.v-icon){
            &:focus:after {
                opacity: 0 !important;
            }
            &.off {
                color: #f0f0f0;
            }
            &.ok {
                color: #47d147;
            }
            &.info {
                color: colours.$info-color;
                @include utils.attentionGrabber;
            }
            &.warning {
                color: colours.$warning-color;
                @include utils.attentionGrabber;
            }
            &.error {
                color: colours.$error-color;
                @include utils.attentionGrabber;
            }
            &.critical {
                color: colours.$error-color;
                @include utils.attentionGrabber;
            }
        }
    }

    .badges {
        
        .badge {
            
            * {
                font-size: 14px;
            }

            &.info {
                * {
                    color: colours.$info-color;
                }
            }

            &.warning {
                * {
                    color: colours.$warning-color;
                }
            }
            &.error {
                * {
                    color: colours.$error-color;
                }
            }
            &.critical {
                * {
                    color: colours.$error-color;
                }
            }
        }
    }

    .details {

        position: relative;
        display: inline-block;
        visibility: hidden;
        cursor: default;

        &:not(:hover) {
            * {
                transition: none !important;
            }
        }

        > div {
            position: absolute;
            min-height: 80px;
            margin-top: calc( -100% - 25px );
            margin-left: 100%;
            text-align: left;

            /* The details themselves */
            > div {
                background: $status-details-color;
                display: flex;
                min-height: 50px;
                margin-left: 10px;
                font-size: 0.8rem;
                color: white;

                border-radius: 10px;
                padding: 4px;
            }

            /* Arrow linking to the main page */
            &:before {
                position: absolute;
                width: 0;
                height: 0;
                border-top: 10px solid transparent;
                border-bottom: 10px solid transparent;
                content: " ";
                border-right: 10px solid $status-details-color;
                margin-top: 10px;
            }
        }
    }
}
