@import '../../Theme/Colors';

.panel {
    .title,
    .docked {
        overflow: hidden;
        label {
            font-size: 0.85rem !important;
            color: $panelmanager-color;
            padding: 5px 5px;

            > div > div > div {
                float: right;
            }
        }
        main {
            margin-left: 0px;
            padding: 0px;
        }

        &.bottom,
        &.top {
            position: absolute;
            background: #202020;
            width: calc( 100vw - 27px );
            left: 27px;
            right: 0px;
            display: flex;
            overflow: visible;
            padding: 0px;
            > section {
                width: 100%;
            }
        }

        &.bottom {
            bottom: 16px;
        }

        &.top {
            top: 0px;
        }
    }

    .settings {
        /*position: absolute;
        margin-left: calc( 100% - 35px );
        margin-top: -50%; */
    }

    .docked {
        padding: 5px 5px;
        white-space: nowrap;
    }

    .dynamic {
        margin-top: 25px;
    }

    .expands {
        margin-bottom: 6px;
        main {
        }

        &:global(.open) {
            header {
            }
        }

        &:global(.closed) {
            margin-bottom: 0px;
            main {
                padding: 0px 15px;
            }
        }

    }

    > :global(.panel){
        border: $element-border;
        background: white;
        position: absolute;
        //width: 420px;
        top: 30px;
        left: 30px;
        //overflow: hidden;
        display: flex;
        flex-direction: column;

        $border: 1px solid #d0d0d0;
        box-shadow: 0px 0px 10px rgba( 0,0,0,0.5 );

        &.grid {
            position: fixed;
            top: 0px;
            /* Temporarily still show access to the old controls */
            //bottom: 0px;
            //right: 0px;
            //FIXME - Comment the following out to avoid full panel widths
            /*
            bottom: 150px;
            right: 250px;

            And replace with ... */

            margin: 2px;
            bottom: 18px;
            right: 0px;


            input:global(.scaleRange#scaleRangeHigh),
            input:global(.scaleRange#scaleRangeLow ) {
                background: transparent !important;
            }

            > header {
                display: none;
            }

            .resize {
                display: none;
            }
        }

        > header {
            -webkit-touch-callout: none; /* iOS Safari */
            -webkit-user-select: none; /* Safari */
            -khtml-user-select: none; /* Konqueror HTML */
            -moz-user-select: none; /* Firefox */
                -ms-user-select: none; /* Internet Explorer/Edge */
                    user-select: none; /* Non-prefixed version, currently
                                        supported by Chrome and Opera */

            font-weight: 300;
            font-size: 1.25rem;
            color: #202020;
            //border-bottom: $border;
            padding: 5px 10px;
            height: 40px;
            min-height: 40px;

            display: flex;
            justify-content: space-between;
            white-space: nowrap;

            > span {
                display: flex;
            }

            .controls {
                margin-top: -11px;
                span {
                    color: $element-border-color;
                    margin: 10px 2px;
                    cursor: pointer;

                    &:hover {
                        color: $header-color;
                    }
                    &:global(.tearoff){
                        transform: scale(-1,1);
                    }
                    &:global(.floating) {
                        display: none;
                    }
                }
            }

            .headertitle {
                text-overflow: ellipsis;
                overflow: hidden;
                display: inline-block;
            }

            /* Hidden mode ... */
            .headerHide {
                display: none;
            }

            &.hide {
                height: 0px;
                min-height: 0px;
                visibility: hidden;
                transition: height 250ms linear;
                position: absolute;
                width: 100%;
                background: white;

                .headerHide {
                    display: block;
                    position: absolute;
                    top: -9px;
                    left: 0px;
                    opacity: 1;
                    visibility: visible;
                    transition: opacity 200ms linear;
                    width: 100%;
                    text-align: center;
                }

                .controls {
                    opacity: 0;
                    transition: opacity 200ms linear;
                }

                &:hover {
                    visibility: visible;
                    height: 35px;
                    min-height: 35px;
                    opacity: 1;
                    z-index: 1;

                    .headerHide {
                        visibility: hidden;
                        opacity: 0;
                    }

                    .controls {
                        opacity: 1;
                        margin-top: -11px;
                    }
                }
            }

            cursor: move;

        }

        > main {
            padding: 10px;
            padding-bottom: 0px;
            overflow: auto;
            height: calc( 100% - 90px );
            flex-grow: 9;
            &.nofooter {
                height: calc(100% - 2px);
            }
            color: $text-color;

            .commonContent {
                height: calc(100% - 50px);
            }
        }

        > footer {
            color: #202020;
            border-top: $border;
            padding: 5px 10px;
            text-align: right;
            max-height: 40px;

            .active {
                float: left;
                div {
                    border: 2px solid #f3f3f3;
                    border-radius: 50%;
                    border-top: 2px solid #3498db;
                    width: 15px;
                    height: 15px;
                    -webkit-animation: spin 1s linear infinite; /* Safari */
                    animation: spin 1s linear infinite;
                }
            }

            .commonFooter {
                position: absolute;
                bottom: 10px;
                height: 40px;
                width: 100%;
            }
        }

        .resize {
            position: absolute;
            bottom: 0px;
            right: 0px;
            width: 10px;
            height: 10px;
            border: 1px #c0c0c0 solid;
            border-radius: 50%;
            background: #c0c0c0;
            cursor: se-resize;
        }

        footer:empty {
            display: none;
        }
        /* Safari */
	    @-webkit-keyframes spin {
		    0% { -webkit-transform: rotate(0deg); }
		    100% { -webkit-transform: rotate(360deg); }
	    }

	    @keyframes spin {
	    	0% { transform: rotate(0deg); }
	    	100% { transform: rotate(360deg); }
	    }
    }

    .unstacked {
        position: absolute;
        top: 30px;
        left: 30px;
        display: flex;
        flex-direction: column;
        bottom: unset !important;
        z-index: -2 !important;

        &.grid {
            position: fixed;
            top: 0px;
            right: 0px;
        }
    }

}


[class^=panelmanager]:not([class*=closed]){
    .docked {
        &.bottom {
            left: 192px;
            width: calc( 100vw - 192px );
        }
    }

}