@import '../../Theme/Colors';

.playback {
    :global( .panel ){
        height: 225px;
        width: 395px;
        max-height: 225px;
        max-width: 395px;
        min-height: 225px;
        min-width: 395px;
    }

    .headercontrols {
        margin-right: 40px;
    }

    .playbackcontrols {
        text-align: center;
        border-radius: 10px;
        padding: 5px;
        color: #808080;

        .active {
            color: #b0b0b0;
        }

        span {
            padding: 3px;
        }
    }
}
