.range {   
    display: flex !important;
    flex-wrap: wrap;
    min-width: 60px;
    width: calc( 100% - 10px );
    white-space: nowrap;

    [class^=menu] nav{
        transform: translateX( -50% );
    }

    &.suggests {
    }
    
    .value {
        width: calc( 50% - 30px )!important;
        display: inline-block;
    }

    > span {
        color: #505050;
        width: 20px;
        display: inline-block;
        font-size: 0.8rem;
        text-align: center;
    }  
}

.units {
    color: #505050;
}

[class^=value] :global(.range) > :global(.value){
    border: none;
    box-shadow: none;
}