@import '../../Theme/Colors';

.menu {
    display: inline-block;
    color: $text-color;

    .button {
        color: green;
    }

    .items {
        border: $element-border;
        background: $menu-background;
        box-shadow: 0px 0px 10px rgba( 0,0,0,0.5 );
        display: none;
        position: absolute;
        z-index: 9999;

        > ul {
            > li {
                padding: 2px 3px;
                cursor: pointer;
                &:hover {
                    color: $info-color;
                }
            }
        }

        &.shown,
        &:hover {
            display: inline;
        }
    }
}