.panel {
    ul {
        list-style: none;
        padding: 0px;
        padding-left: 20px;

        li {
            border-bottom: 1px solid #c0c0c0;
            margin-bottom: 5px;
            label {
                margin-right: 10px;
                width: 100%;
                display: inline-block;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;

                .method {
                    color: #0099ff;
                }
            }
        }

        span {
            
        }
    }
}