.notifications {
    .notification {
        padding: 0px;
        -webkit-user-select: text;
	    -khtml-user-select: text;
 	    -moz-user-select: text;
	    -ms-user-select: text;
 	    user-select: text;
    }

    .ok {
        :global(.v-avatar) :global(.v-icon) {
            color: #155724;
            background-color: #d4edda;
            border-color: #c3e6cb;
        }
    }

    .warning {
        :global(.v-avatar) :global(.v-icon) {
            color: #e67300;
            background-color: #fff3cd;
            border-color: #ffeeba;
        }
    }

    .error {
        :global(.v-avatar) :global(.v-icon) {
            color: #721c24;
            background-color: #f8d7da;
            border-color: #f5c6cb;
        }
    }

    .info {
        :global(.v-avatar) :global(.v-icon) {
            color: #0c5460;
            background-color: #d1ecf1;
            border-color: #bee5eb;
        }
    }

    .detail {
        margin-left: 55px;
        font-style: italic;
    }
}