















:global(#app) {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100vh;
  //width: calc( 100% - 150px );;
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;

  // FIXME - Comment this out to avoid full screen panels
  width: 100vw;
  background: white;

  .fotechApp {
    font-family: "PT Sans","Helvetica Neue",Helvetica,Roboto,Arial,sans-serif;
    position: absolute;
    top: 0px;
    left: 0px;
    display: flex;
    z-index: 1;
    margin-left: -1px;
  }
}
