@use '../../../Theme/Colors' as colors; 

.playback {
    font-size: 10px;

    input:focus {
        outline: none;
    }

    .expander {
        width: 30px;
        position: absolute;
        right: 0px;
        bottom: 10px;
        cursor: pointer;
    }

    .content {
        display: flex;
        width: calc( 100% - 30px );

        /* Flex definitions, done here to allow them to be obviously related
           to each other and controlled */
        flex-wrap: wrap;  // Allow items to take up two rows
        align-items: flex-end;
        align-content: stretch;

        .switcher {
            order: 1;
            flex: 1 2 auto;
        }
        .controls {
            order: 2;
            flex: 2 1 auto;
            display: flex;
            flex-wrap: wrap;  // Allow items to take up two rows
            align-items: flex-end;
            align-content: stretch;

            .status {
                order: 1;
                flex: 1 1 auto;
            }
            .playbackbar {
                order: 2;
                flex: 1 1 auto;
            }
        }
    }

    /* File switcher and selection controls */
    .switcher {
        :global([class^=boolean_]){
            padding-top: 3px;
            padding-left: 12px;
        }

        display: flex;
        padding-bottom: 11px;
        overflow: hidden;
        font-size: 11pt;
        color: #f0f0f0;

        .title {
            flex-grow: 9;
            padding: 0px 5px;
            overflow: hidden;
            text-overflow: ellipsis;
            display: inline-flex;
            width: 100%;
            flex-shrink: 1;

            .sessionName {
                display: flex;
                .first {
                    flex: 0 1 auto;
                    display: inline-block;
                }
                .last {
                    flex: 0 0 auto;
                    display: inline-block;
                }
            }

            span {
                flex-grow: 9;
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
    }

    /* Time and Distance selection controls */
    .selectors {
        height: 0px;
        overflow: hidden;
        display: flex;
        font-size: 8pt;

        :global(.v-toolbar){
            height: 28px !important;
            > * {
                height: 28px !important;
            }
            :global(.v-toolbar__title){
                font-size: 8pt;
                color: #b0b0b0;
            }
            span {
                margin-left: 3px;
            }
        }

        input {
            background-color: rgba(0,0,0,0.2) !important;
            color: #f0f0f0;
            border: 1px solid #303030 !important;
            border-radius: 10px;
            margin-left: 5px;
            text-align: center;

            &.invalid {
                border: colors.$error-border !important;
            }
        }

        :global(.theme--light){
            input {
                background-color: #f0f0f0 !important;
                color: black;
            }
        }

    }

    .expanded {
        .selectors {
            height: auto;
        }
    }

    /* Time selection and playback scrubber */
    .status {
        padding: 0px 5px;
        :global(.v-progress-linear){
            transition: 0s;
        }
    
        .progress {
            width: calc( 100% );
            margin-left: 0px;
        }
    }

    /* Control buttons */
    .playbackbar {
        padding-bottom: 2px;
        .playbackcontrols {
            margin-top: 5px;
            text-align: center;
            border-radius: 10px;
            padding: 5px;
            color: #808080;
            width: fit-content;
            margin-left: auto;
            margin-right: auto;

            .active {
                color: #b0b0b0;
            }

            span {
                padding: 3px;
            }

            .custom {
                background: white !important;
                color: #505050;
            }
        }
    }

    &.dark {
        .playbackcontrols {
            background: #505050;
        }
        .expander {
            :global( .v-icon ){
                color: #e0e0e0;
            }
        }
    }

    pre {
        font-size: 7px;
    }

    .radio {
        height: 25px;
        width: 25px;
    }

    .radioLabel {
        width: auto;
    }
}