@import '../../../Theme/Colors';

.repeated {
    display: flex;

    .control {
        text-align: right;
        height: 20px;
        .add {
            color: green;
            font-size: 1.2em;
            position: absolute;
            right: 0px;
        }
    }

    :global(.items) {
        border-right: $element-border;
        padding: 0px !important;
        margin-top: 0px;

        li {
            &:global(.active) {
                border-right: 1px solid white !important;
                background: white;
            }
            border: $element-border;
            border-top-left-radius: 5px;
            border-bottom-left-radius: 5px;
            margin-right: -1px;
            display: block;
            border-right: $element-border;
            background: #e0e0e0;
            padding: 5px;
            white-space: nowrap;
            margin-bottom: 2px;
            cursor: pointer;
        }
    }

    :global(.entries) {
        border-top: $element-border;
        padding: 5px;
        margin-left: -1px;
        width: 100%;
        overflow: auto;

        > div {
            display: none;
        }
        :global(.active) {
            display: inline;
        }
        > [ class^=property]{
            > label {
                display: none !important;
            }
        }
        [class^=property] {
            > label {
                display: block;
                width: fit-content;
                overflow: visible;
            }
            [class^=value] {
                width: calc( 100% - 10px );
                min-width: 60px;

                /* Repeated values are *often* different from their known values
                   and having lots of messages about it is both distracting and
                   overkill, so hide them */
                &:hover {
                    [class^=changed],
                    [class^=updated] {
                        //display: none;
                    }
                    [class^=notes] {
                        display: none;
                    }
                }
                [class^=multiple_]{
                    padding-left: 5px;
                    ul {
                        padding: 0px !important;
                    }
                }
            }
        }
    }

    &.list{
        :global(.entries) {
            padding: 5px;
            width: 100%;
            border: none;
            $labelWidth: 50px;

            > li[class^=property] {
                > label {
                    display: inline-block;
                    width: $labelWidth;
                }
                > [class^=value] {
                    display: inline-block;
                    width: calc( 100% - (#{$labelWidth} + 20px) );
                    padding: 0px;
                    > :global(.value){
                        border: none;
                        box-shadow: none;
                        padding: 0px;
                        border-bottom: $element-border;
                    }
                }
                &:not(.first) {
                    > [class^=value] {
                        label {
                            display: none;
                        }
                    }

                    [class^=title] {
                        display: none;
                    }
                }
            }
            .remove {
                position: absolute;
                right: 0px;
                font-size: 1.2em;
                color: $danger-color;
            }
            .first {
                > label {
                    padding-top: 30px;
                }
                .remove {
                    top: 30px;
                }
            }
        }
    }


    color: $element-color;

    [class^=changed] {
        color: black;
    }

    .changed {
        color: $changed-color;
    }

    .updated {
        color: $updated-color;
    }
}

:global(.repeated) {
    [class*=changed] {
        :global(.changed),
        :global(.errors),
        :global(.updated) {
            display: none;
        }
    }
    :global(.notes) {
        display: none;
    }
}