.progress {
    width: 100%;
    padding-right: 10px;
    
    .runner {
        display: flex;
        .start, .end {
            width: 50px;
            flex: 0 9 auto;
            font-size: 14px;
        }
        .start {
            text-align: right;
        }
        .bar {
            flex: 9 0 auto;
            padding-top: 4px !important;
            padding-left: 10px;
            padding-right: 10px;
        }
    }

    .bar {
        .position {
            display: none;
            position: relative;
            width: 100%;
            z-index: 8;

            .marker {
                background: #404040;
                color: white;
                position: absolute;
                z-index: 9;

                margin-top: 8px;
                padding: 5px;
                width: fit-content;
                border-radius: 5px;
                transform: translateX( -50% );

                &:before {
                    display: block;
                    position: absolute;
                    content: " ";
                    border-bottom: 5px solid #404040;
                    border-left: 2.5px solid transparent;
                    border-right: 2.5px solid transparent;
                    border-top: 0px;
                    width:0px;
                    height: 0px;
                    top: -5px;
                    left: calc( 50% - 2.5px );
                }
            }
        }

        &:hover .position {
            display: block
        }


        .selecting {
            position: relative;
            width: 100%;
            .selector {
                background: yellow;
                height: 20px;
                width: 30px;
                position: absolute;
                margin-top: -20px;
                opacity: 0.6;
            }
        }
    }

    .current {
        display: block;
        text-align: right;
        min-height: 15px;
    }
}

:global( .docked_bottom ),
:global( .docked_top ){
    margin-top: 0px;
    .markers {
        flex-basis: 100%;
        font-size: 9px;

        .menu {
            transform: translate( -78%, -108% );
        }
    }
}