/* Reset any Vuetify overrides */
$selector: ":not([class^=v-])";

textarea#{$selector},
input#{$selector}:not([type=image]),
{
    border: 1px solid #c0c0c0;
    background: white;
}

select#{$selector} {
    -webkit-appearance: menulist;
    -moz-appearance: menulist;

    &:not([class^=list-]){
        background: buttonface;
        border-style: solid;
    }

    * {
        all: unset;
    }

    > option {
        display: list-item;
    }
}

button#{$selector},
input[type=button]#{$selector},
input[type=submit]#{$selector},
input[type=reset]#{$selector},
{
    -webkit-appearance: button;
    -moz-appearance: button;   
    border-style: outset;   
    border: 1px solid silver;
    border-radius: 5px;
    padding: 3px;
    background: #ffffff !important ;
    margin-bottom: 1px;
    margin-top: 1px;
}

html {
    /* Remove the vertical scrollbar */
    overflow-y: hidden !important ;
}