@use '../../../Theme/Colors' as colors; 

.playback {
    font-size: 10px;
    input {
        background-color: rgba(0,0,0,0.2) !important;
        color: #f0f0f0;
        border: 1px solid #303030 !important;
        border-radius: 5px;
        margin-left: 3px;
        text-align: center;
        width: 50px;

        &.invalid {
            border: colors.$error-border !important;
        }
    }

    :global(.v-toolbar){
        span {
            margin-left: 3px;
        }
    }

    :global(.v-progress-linear){
        transition: 0s;
    }

    .switcher {
        :global([class^=boolean_]){
            padding-top: 3px;
            padding-left: 12px;
        }

        display: flex;

        .live {
            /* Mark the text as disabled in live mode */
            color: #505050;
        }

        .title {
            flex-grow: 9;
            padding: 0px 5px;
            overflow: hidden;
            text-overflow: ellipsis;
        }
    }

    .status {
        > main {
            position: relative;
            width: calc( 100% );

            :global(.v-toolbar){
                height: 28px !important;
                > * {
                    height: 28px !important;
                }
            }
        }
    }

    .progress {
        width: calc( 100% );
        margin-left: 0px;
    }

    .playbackcontrols {
        margin-top: 5px;
        text-align: center;
        background: #505050;
        border-radius: 10px;
        padding: 5px;
        color: #808080;
        width: fit-content;
        margin-left: auto;
        margin-right: auto;

        .active {
            color: #b0b0b0;
        }

        span {
            padding: 3px;
        }
    }
pre {
    font-size: 7px;
}
}