@import '../../Theme/Colors';

.yoink {    
    main {
        height: 100%;
        .contents {            
            height: 100%;
            table {
                > tbody {
                        > tr > td > div {                      
                            max-width: 440px;
                            overflow-x: auto;
                        }
               }
            }
        }        
    }

    /* Remove the Yahoo elements we don't want */
    :global(.hd),
    :global(.container-close),
    :global(.yui-resize-handle)
    {
        display: none;
    }

    button {
        margin-left: 8px;
    }

    /* Overrides of background and colours and other YUI options */
    :global( .yui-panel ){
        border: 0px !important;

        input,
        select,
        textarea {
            border: $element-border;
            border-radius: $element-radius;
            padding: $element-padding;
            background: $element-background;
            margin-bottom: 1px;
            margin-top: 1px;
        }

        :global(.bd) {
            background-color: transparent !important;
            border: 0px !important;
            padding: 0px;
            :global(.yui-layout){
                background-color: transparent;
                :global(.yui-layout-doc) {
                    border: 1px solid transparent;
                    padding: 0px;

                    :global(.yui-layout-bd){
                        background-color: transparent;
                        > div {
                            overflow: hidden;
                        }
                    }

                    div {
                        box-sizing: border-box;
                    }
                }
            }

            /* Normal components */
            fieldset {
                border: 0px;
                margin: 0px;
                padding: 8px 0px;

                legend {
                    @include title();
                }

                label {
                    color: $label-color;
                    font-weight: normal;
                }
            }

            /* Fotech Core components */
            .coreSlider {
                height: 45px;
            }


            table {
                > tbody,
                > thead {
                    > tr > th {
                        width: $labelwidth;
                        color: $label-color;
                        font-weight: normal;
                        padding: 3px;
                    }
                }
            }

            :global(.yui-content) {
                border: 0px;
                background-color: transparent;
            }

            :global(.yui-nav){
                border: 0px;
                border-bottom: 1px solid #d0d0d0;

                li {
                    background-color: transparent;
                    border: 1px solid transparent;
                    color: $label-color;

                    a {
                        background: transparent;
                        border: 1px solid transparent;
                        color: $label-color;
                        padding: 5px;
                    }

                    &:global(.selected) {
                        border-bottom: $info-border;
                        margin: 0px;
                        color: $label-color;
                        a {
                            font-weight: bold;
                            border-bottom: $info-border;
                        }
                    }

                }
            }

            :global(.note){
                white-space: normal;
                display: inline-block;
                overflow-wrap: break-word;
            }

            :global( .default .coreCheckbox .slider.on .on ),
            :global( .default .coreCheckbox .slider.off .off ){
                position: absolute;
                margin-top: -7px;
            }

            ul:global(.navlist){
                li {
                    cursor: pointer;
                }
                li:global( .current ){
                    color: $info-color;
                }
            }
        }
    }

    &.canvas {
        main {
            > main {
                width: 100%;
                height: calc( 100% - 10px);
                > section {
                    width: 100%;
                    height: 100%;
                    > canvas {
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }
}

:global(.mask){
    visibility: hidden;
}

/* Curious include to fix the layout of Zone Properties specifically */
:global( #zone_properties_dialog fieldset ){
    width: 575px;
}

:global(.zone_list_dialog_description) {
    max-width:  250px;
    overflow:   hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
