.logging {
    ul {
        list-style: none;
        padding-left: 5px;
    }

    .recording {
        font-size: 10pt;
        color: red;
        top: 5px;
        left: 5px;
        position: absolute;
        animation: flash linear 1s infinite;
    }

    .heading {
        font-weight: bold;
    }
}

@keyframes flash {
    0% { opacity: 1; }
    50% { opacity: 0.5; }
    100% { opacity: 1; }
}
