@import '../../../Theme/Colors';

.value {
    display: inline-block;
    flex-grow: 9;
    vertical-align: top;
    font-size: 0.8rem;
    margin-bottom: 5px;
    position: relative;

    :global(.value) {
        :global(.dark) &{
            background-color: transparent;
            color: white;
            input {
                background-color: transparent;
                color: white;
            }
        }

        [readonly] {
            color: #808080;
        }
        border: $element-border;
        border-radius: $element-radius;
        padding: $element-padding;
        box-shadow: $element-shadow;

        input,
        input:active {
            border: none;
            width: 100%;
            padding-left: 4px;
        }

        select {
            background: $element-background;
            border-style: none;

            &[disabled] {
                opacity: 0.7;
            }
        }

        :global(.component){
        }

        &.updated,
        &.changed,
        &.error {
            $width: 22px;
            > input,
            > select,
            > textarea,
            > div {
                width: calc( 100% - #{$width} );
                display: inline-block;
            }
        }

        &.changed {
            border: $changed-border;
            .error {
            }
        }

        &.updated {
            border: $updated-border;
            .error {
                color: $updated-color;
            }
        }

        &.error {
            border: $error-border;
            padding: 0px;            

            .error {
                color: $error-color; 
                padding-right: 2px;               
            }
        }

        input:focus {
            outline: none;
        }

        div:focus {
            outline: none;
        }
    }

    color: $element-color;

    .changed {
        color: $warning-color;
    }

    .updated {
        color: $info-color;
    }

    .error,
    .note {
        max-height: 500px;
        overflow: hidden;
        transition: max-height 1s ease-in-out;
        outline: none;
        white-space: normal;
        ul {
            list-style: none;
            padding: 0px;
        }
        &:empty {
            max-height: 0px;
        }
    }

    &:hover {
        :global( .notes ){
            .changed {
                display: block;
            }
        }
    }

    :global(.notes) {
        width: auto !important;

        .changed {
            color: white !important;
            background: $warning-color;
            position: absolute;
            padding: 3px;
            border-radius: 5px;
            transform: translateY( 5px );
            z-index: 1;
            display: none;

            &:before {
                display: block;
                position: absolute;
                content: " ";
                border-bottom: 5px solid $warning-color;
                border-left: 2.5px solid transparent;
                border-right: 2.5px solid transparent;
                border-top: 0px;
                width:0px;
                height: 0px;
                top: -5px;
                left: calc( 50% - 2.5px );
            }
        }
    }

    .additional {
        text-align: right;
    }

    .error {
        color: $error-color;
    }

    .note {
        color: $note-color;
    }

    .value.full, .value > div {
        width: 100%;
    }
}