@import '../../Theme/Colors';

.title {
    > header {
        width: 100%;
        cursor: pointer;
        color: $header-color;
        $iconwidth: 20px;
        display: flex;
        justify-content: space-between;

        >label {
            @include title();
            cursor: pointer;

            display: inline-block;
            width: 100% !important;
            padding: 8px 5px;
            position: relative;
        }

        .controls {
            position: absolute;
            top: 0px;
            right: 0px;
            text-align: right;
        }

        .marker {
            display: inline-block;
            font-size: 1.4rem;
            width: $iconwidth;
            text-align: right;
            color: $header-color;
        }

        .safariMarker {
            font-size: 0.65em;
            padding-right: 2px;
        }

        .headerOpen,
        .headerClosed {
            * {
                color: unset;
                transition: transform 300ms linear;
            }
        }

        .headerOpen {
        }

        .headerClosed {
            * {
                transform: rotate(180deg);
            }
        }

        .settingsMarker {
            padding-top: 4px;
            display: inline-block;
            font-size: 1rem;
            width: $iconwidth;
            text-align: right;
            color: $border-color;

            &:hover {
                .settings {
                    display: block !important;
                }
            }


        }

        .settingsOpen {
            color: $info-color;
        }

        .settingsClosed {
            color: #505050;
        }        
    }

    .settings {
        background: #202020;
        color: $panelmanager-color;
        padding: 5px 15px;
        position: absolute;
        z-index: 9;
        width: 200px;
        margin-left: 10px;
        box-shadow: #000000 0px 0px 15px;
        border: 1px solid #505050;
        border-radius: 10px;

        ul {
            list-style: none;
            padding: 0px;
            li {
                label {
                    display: inline-block;
                    width: 80px !important;
                    text-overflow: ellipsis;
                    overflow: hidden;
                }
            }
        }
    }

    .main {
        padding: 0px;
        transition: max-height 200ms ease-in-out,
                    margin 100ms ease-in-out,
                    padding 100ms ease-in-out;

        &.open {
            //border-bottom: $element-border;
        }

        &.closed {
            max-height: 0px;
            overflow: hidden;
        }
    }

    &.submenu {
        > header {
            > label {
                font-size: 0.85rem !important;
                color: $panelmanager-color;
                padding: 5px;
            }
        }

        > .main {
            background-color: rgba( 40, 40, 40, 0.6 );
            > main {
                margin-left: 10px;
                > div {
                    > ul {
                        list-style: none;
                        padding-left: 0px;
                        li {
                            padding: 5px 5px;
                            a, a:hover, a:active, a:visited {
                                color: $panelmanager-color;
                                text-decoration: none;
                            }
                        }
                    }
                }
            }
        }
    }
}
