@import '../../Theme/Colors';

.alert {
    ul {
        list-style: none;
        padding: 0px;

        li {
            label {
                display: inline-block;
                width: 120px;
                min-width: 120px;
                font-weight: bold;
            }
        }
    }

    header.threat {
        border: $element-border;
        border-radius: 5px;
        padding: 5px;

        $iconwidth: 80px;
        white-space: nowrap;

        .icon {
            width: $iconwidth;
            text-align: center;
            float: left;
        }

        ul {
            margin-left: $iconwidth + 20px;
            label {
                width: 80px;
            }
            span {
                white-space: pre-wrap;
            }
        }

        &:global(.threat_red) {
            border: $error-border;
            background: $error-background;
        }

        &:global(.threat_amber) {
            border: $warning-border;
            background: $warning-background;
        }

        &:global(.threat_green) {
            border: $success-border;
            background: $success-background;
        }

        &:global(.threat_clear) {
            border: $info-border;
            background: $info-background;
        }
    }

    main {
        > ul {
            margin-bottom: 10px;
            margin-top: 10px;
            padding-bottom: 10px;
            border-bottom: $element-border;
        }

        .source {
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        .sensor {
            color: $info-color;
            font-weight: bold;
        }

        :global(.response_escalate){
            color: $error-color;
        }

        :global(.response_downgrade){
            color: $success-color;
        }

        :global(.response_retrigger){
            color: $warning-color;
        }

        :global(.response_received){
            color: $info-color;
        }

        :global(.response_acknowledge){
            color: $info-color;
        }

        :global(.response_resolve){
            color: $success-color;
        }

        :global(.response_confirm){
            color: $info-color;
        }

        .duplicates {
            font-size: 9pt;
            position: relative;
            display: none;
            span {
                color: white;
                border-radius: 5px;
                background: tomato;
                padding: 0px 2px;
                position: absolute;
            }
        }

        display: flex;
        flex-direction: column;

        &.details {
            display: flex;
            flex-direction: column;
            flex-grow: 9;
            overflow: auto;

            ul {
                li {
                    display: flex;
                    > label, > span {
                        display: flex;
                        overflow: hidden;
                        text-overflow: ellipsis;
                    }
                }
            }
        }

        .responses {
            display: flex;
            flex-direction: column;
            overflow: hidden;
            min-height: 60px;

            div {
                display: flex;
                flex-direction: column;
                overflow: hidden;

                > table {
                    $scrollbar-width: 0.9em;
                    display: flex;
                    flex-flow: column;
                    height: 100%;
                    font-size: small;

                    thead,
                    tbody tr {
                        display: table;
                        table-layout: fixed;

                        td, th {
                            &:nth-child( 1 ){
                                width: 110px;
                            }

                            &:nth-child( 2 ){
                                width: 80px;
                            }

                            &:nth-child( 3 ){
                                width: 120px;
                            }
                        }
                        div.date {
                            font-size: 0.7em;
                        }
                    }

                    thead {
                        flex: 0 0 auto;
                        width: calc(100% - #{$scrollbar-width});
                    }

                    tbody {
                        display: block;
                        flex: 1 1 auto;
                        overflow-y: scroll;

                        tr {
                            width: 100%;
                        }
                    }
                }
            }
        }
    }

}