.loading {
    padding: 40px;
    margin-left: auto;
    margin-right: auto;
    color: #0099ff;
    text-align: center;
    overflow: hidden;
    border: 1px solid #c0c0c0;
    border-radius: 5px;
    background: white;
    box-shadow: 0px 0px 10px 0px #808080;

    @mixin animate( $prop ){
        -webkit-transition: $prop 500ms ease-in;
        -moz-transition: $prop 500ms ease-in;
        -o-transition: $prop 500ms ease-in;
        -ms-transition: $prop 500ms ease-in;
        transition: $prop 500ms ease-in;
    }

    transition: visibility 1s, opacity 1s linear;
    visibility: visible;
    display: block;
    opacity: 1;


    &.hidden {
        visibility: hidden;
        opacity: 0;
    }
}

$width: 500px;
$duration: 4s;
$fsize: 20px;
$pwidth: 26px;

.laser {
    $pulse-color: #ff9100;
    border-bottom: 1px solid $pulse-color;
    border-top: 1px solid $pulse-color;
    height: 28px;
    background: linear-gradient(to bottom, #f0f0f0 0%,#ffffff 50%,#f0f0f0 100%);

    margin-left: auto;
    margin-right: auto;
    position: relative;

    .fibre {
        border-bottom: 3px solid $pulse-color;
        width: 100%;
        margin-top: 13px;
    }

    .pulse {
        animation: pulse-wrapper $duration linear infinite;
        width: 100%;
        height: $pwidth;
        position: absolute;
        text-align: left;
        left: 0px;
        span {
            animation: pulse $duration linear infinite;
            width: $pwidth;
            height: $pwidth;
            position: absolute;
            background-color: $pulse-color;
            margin-top: 2px;
            border-radius: 50%;
            margin-left: calc( ($pwidth / 2) * -1 );
        }
    }

    .found {
        font-size: $fsize;
        width: 100%;
        text-align: left;

        div {
            margin-top: -12px;
            //margin-top: 2px;
            position: absolute;
            animation-iteration-count: infinite;
            animation-timing-function: linear;
            animation-duration: $duration;
            text-align: left;
            width: 100%;
        }

        @for $i from 1 through 4 {
            div:nth-child(#{$i}) {
                animation-name: find-#{$i}-wrapper;
                * {
                    //animation: find-#{$i} $duration linear infinite;
                }
            }
        }

        div:nth-child(2) {
 //           animation-name: find-2-wrapper ;
        }
        div:nth-child(3) {
            //animation-name: find-3;
        }
    }

    .loadinglabel {
        margin-top: 10px;
    }
}

@keyframes pulse-wrapper {
    0% {
        transform: translateX( 0% );
    }
    50% {
        transform: translateX( 50% );
    }
    51% {
        transform: translateX( 50% );
    }
    100% {
        transform: translateX( 100% );
    }
}

@keyframes pulse {
    0% {
        transform: scale( 0 );
        opacity: 0;
    }
    1% {
        transform: scale( 1 );
        opacity: 1;
    }
    75% {
        transform: scale( 0.5 );
    }
    100% {
        transform: scale( 0 );
        opacity: 0;
    }

}

@keyframes find-1 {
    0% {
        transform: scale( 0 );
    }
    45% {
        transform: scale( 0 );
    }
    50% {
        transform: scale( 1 );
    }
    60% {
        transform: scale( 1 );
    }
    75% {
        transform: scale( 0 );
    }
    100% {
        transform: scale( 0 );
    }
}

@keyframes find-1-wrapper {
    0% {
        transform: translateX( 50% );
        opacity: 0;
    }
    50% {
        transform: translateX( 50% );
        opacity: 0;
    }
    51% {
        opacity: 1;
    }
    65% {
        transform: translateX( 0% );
        opacity: 1;
    }
    66% {
        opacity: 0;
    }
    100% {
        opacity: 0;
    }
}

@keyframes find-2 {
    0% {
        transform: scale( 0 );
    }
    20% {
        transform: scale( 0 );
    }
    35% {
        transform: scale( 1 );
    }
    30% {
        transform: scale( 1 );
    }
    45% {
        transform: scale( 0 );
    }
    100% {
        transform: scale( 0 );
    }
}

@keyframes find-2-wrapper {
    0% {
        transform: translateX( 25% );
        opacity: 0;
    }
    25% {
        transform: translateX( 25% );
        opacity: 0;
    }
    26% {
        opacity: 1;
    }
    35% {
        transform: translate( 0% );
        opacity: 1;
    }
    36% {
        opacity: 0;
    }
    100% {
        transform: translate( 0% );
        opacity: 0;
    }
}

@keyframes find-3-wrapper {
    0% {
        transform: translateX( 75% );
        opacity: 0;
    }
    75% {
        transform: translateX( 75% );
        opacity: 0;
    }
    76% {
        opacity: 1;
    }
    95% {
        transform: translate( 0% );
        opacity: 1;
    }
    96% {
        opacity: 0;
    }
    100% {
        transform: translate( 0% );
        opacity: 0;
    }
}

@keyframes find-3 {
    0% {
        transform: scale( 0 );
    }
    70% {
        transform: scale( 0 );
    }
    75% {
        transform: scale( 1 );
    }
    90% {
        transform: scale( 1 );
    }
    95% {
        transform: scale( 0 );
    }
    100% {
        transform: scale( 0 );
    }
}