@use '../../Theme/Colors';

.buttonmenu {
    margin: 0px;
    box-sizing: border-box;
    display: inline-block;

   .menu {
        /* The actual menu contents */
        opacity: 0;
        visibility: hidden;
        display: none;
        list-style: none;
        position: absolute;
        padding: 0px;
        margin: 0px;
        z-index: 1;
        border: 1px solid #c0c0c0;
        background: white;
        box-shadow: 5px 5px 3px 0px rgba(0,0,0,0.3);

        input, select {
            width: 50px;
        }
    }

    &.dark {
        color: white;
        .menu {
            border: 1px solid #303030;
            background: #202020;
        }
    }

    &:hover {
        .menu {
            opacity: 1;
            visibility: visible;
            display: block;
        }
    }
}

:global(.docked_bottom) {
    .buttonmenu {
        &:hover {
            .menu {
                transform: translate( -75%, -108% );
                box-shadow: 5px -5px 3px 0px rgba(0,0,0,.3);
            }
        }
    }
}

:global(.docked_top) {
    .buttonmenu {
        &:hover {
            .menu {
                transform: translate( -75%, 0% );
            }
        }
    }
}

@keyframes fade {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}