@import '../../../Theme/Colors';

.multiple {

    .control {
        color: green;
        text-align: right;
        margin-right: 4px;
    }

    .remove {
        display: inline-block;
        margin-top: 8px;
        color: red;
    }


    section {
        width: calc( 100% - 20px ) !important;

        [class^=value]{
            width: calc( 100% - 10px );
        }
    }
}

[class^=value] :global(.multiple) > :global(.value){
    border: none !important;
    box-shadow: none;
}

:global(.multiple) {
    > :global(.value) {
        > :global(.changed),
        > :global(.errors),
        > :global(.updated) {
            display: none;
        }
    }
    > :global(.notes) {
        display: none;
    }
}