.health {
    ul {
        list-style: none;
        padding-left: 5px;
        width: 150px;
        li {
            display: flex;
            > label {
                display: flex;
                flex-direction: column;
                width: 100px;
                span.name {
                    /* Sublabels which show the name of the GPU etc */
                    color: #a0a0a0;
                    font-size: .5rem;
                }
            }
        }
    }
}