@use '../../Theme/Colors.scss' as colors;

.multiplexing {
   
    .controls {
        height: 40px; 
        text-align: right;
    }

    .remove {
        margin-bottom: 10px;
        &:hover {
            color: red;
        }
    }

    [class^=property] > section {
        width: auto;
    }

    > section {
        display: flex;
        align-items: flex-end;

        label {            
            width: ( 130px !important ) ;
            overflow: hidden;
            text-overflow: ellipsis;
        }

        > label {
            display: inline-block;
            padding-bottom: 5px;
        }

        &:not(:first-of-type):not( div[class^=notes] ){
            :global( ul[class^=accordion] ){
                :global( li[class^=property] ){
                    label {
                        /* Hide the text, but force it to reflow
                            and instigate widths */
                        height: 0px;
                        visibility: hidden;
                    }
                }
            }
        }

        :global( ul[class^=accordion] ){          
            display: flex;
            :global( li[class^=property] ){                
                margin-left: 4px;
                display: flex;
                flex-direction: column;

                label {
//                    display: none;
                }
            }
        }
    }

    .assignments {
        .optics {
            margin-left: 200px;
        }
        > section {
            display: flex;
            > label {
                display: inline-block;
                width: 200px;
                overflow: hidden;
                text-overflow: ellipsis;
                text-align: center;
            }

            .changed {
                color: colors.$changed-color;
            }
        
            .updated {
                color: colors.$updated-color;
            }

            .error {
                color: colors.$error-color;
            }

            > div {
                display: inline-block;
                width: 50px;
                overflow: hidden;
                text-overflow: ellipsis;
                text-align: center;
                border-right: 1px solid #f0f0f0;
            }
        }
    }

}