@import '../../Theme/Colors';

.statusbar {
    &.hidden {
        display: none;
    }

    height: 100vh;
    background: $fotech-darkgrey;
    width: 27px;
    padding-top: 10px;
    font-size: 1.0rem;
    text-align: center;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 20px;

    :global(.status) {
        width: 26px;
        color: #f0f0f0;
        margin-top: 5px;
        cursor: pointer;

        &:global(.on){
            //color: #47d147;
        }

        > div {
            > div {
                //font-size: 0.44rem;
            }
        }

        :global(.marker) {
            transform: translate( 10px, -25px );
            font-size: 0.9rem;
            color: $fotech-lightgrey;

            &:global(.on){
                color: $fotech-green;
            }
        }
    }
}

.systembar {
    &.hidden {
        display: none;
    }

    height: 18px;
    width: 100vw;

    position: absolute;
    bottom: 0px;
    left: 0px;

    background: $fotech-darkgrey;
    color: #e0e0e0;
    font-size: 8pt;

    :global(.v-icon){
        color: inherit;
        font-size: inherit;
    }

    padding: 0px 8px;
    text-align: right;
    div {
        margin-left: 10px;
        display: inline;
    }
}