.tree {
    background-color: white;

    .open {
    }

    .closed {
        display: none;
    }

    list-style: none;
    padding: 0px;
    padding-left: 20px;

    :global(.icon) {
        margin-right: 5px;
        color: #606060;
        font-size: 7pt;
        display: inline-block;
        width: 10px;
    }

    .expand {
        margin-right: 5px;
        font-size: 8pt;
    }

    label {
        display: inline-block;
        min-width: 90px;
    }

    div {
        display: inline-block;
    }

    li {
        white-space: nowrap;
    }
}