@use '../../Theme/Colors' as colours;

.nestedmenu {
    $width: 154px;
    $box-height: 28px;
    background: white;
    margin: 0px;
    box-sizing: border-box;
    width: $width;

    &:not(.isChild){
        border: colours.$element-border;
        border-radius: colours.$element-radius;
        height: 25px;
        > .list {
            margin-left: 0px;
            margin-top: -5px;
        }
    }
    
    &:hover {
        background: lightcyan;
    }

    &.nested {
        /* Used when a menu has nested items */
        &:hover {
            > .list {
                opacity: 1;
                display: block;
                visibility: visible;
                animation: fade 300ms;
            }
        }

        > .item {
            label {
                padding: 0px;
                display: inline-block;
                width: 100%;
                position: relative;

                &:after {
                    content: " ";
                    border: 5px solid #c0c0c0;
                    border-top-color: transparent;
                    border-bottom-color: transparent;
                    border-right: 1px solid transparent;
                    margin-top: 4px;
                    position: absolute;
                    right: 0px;
                }
            }
        }
    }
    .item {
        /* An individual item */
        box-sizing: border-box;
        height: $box-height;
        padding: 6px;
        overflow: hidden;
        text-overflow: ellipsis;
        label {
            color: #303030;
            display: inline-block;
            width: 100%;
            padding: 0px;
            cursor: pointer;
            span {
                width: 100%;
                display: inline-block;
            }
        }
    }
    .list {
        /* The list of nested items */
        opacity: 0;
        visibility: hidden;
        display: none;
        list-style: none;
        position: absolute;
        padding: 0px;
        margin: 0px;
        z-index: 1;
        margin-left: $width;
        margin-top: -#{$box-height};
        border: 1px solid #c0c0c0;
        box-shadow: 5px 5px 3px 0px rgba(0,0,0,0.3);
        li {
            box-sizing: border-box;
            height: $box-height;
            &.disabled {
                label {
                    color: #c0c0c0;
                }
            }
        }
    }
}

@keyframes fade {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}