@import '../../Theme/Colors';

.events {
    input {
        color: black;
    }

    .zoom {
        text-align: center;
        display: flex;
        flex-direction: column;

        .small, .medium, .large {
            color: #e0e0e0;
            margin-left: auto;
            margin-right: auto;
        }
        .small {
            font-size: 15px;
        }
        .medium {
            font-size: 20px;
        }
        .large {
            font-size: 30px;
        }
    }

    .tracks {
        display: block;
        font-size: 9px;
        color: #909090;
        overflow: hidden;
        overflow-y: auto;
        max-height: 100px;
        @include customScroll();
    }

    section {
        width: 100%;
        max-width: 50vw;
        overflow: auto;

        @include customScroll();

        ul {
            list-style: none;
            display: flex;
            padding: 0px;
            li {
                width: 80px;
                min-width: 80px;
                text-align: center;
                margin: 5px;
                .path {
                    font-size: 14px;
                }
                .icon {
                    $size: 40px;
                    border: 3px solid red;
                    background: white;
                    border-radius: 50%;

                    margin-left: auto;
                    margin-right: auto;

                    width: $size + 4;
                    height: $size + 4;

                    * {
                        width: $size;
                        height: $size;
                        margin-left: auto;
                        margin-right: auto;
                    }
                }
            }
        }
    }
}