@import '../../Theme/Colors';

.accordion {
    > li {
        &.section {
            border-bottom: $element-border;
        }

        > header {
            width: 100%;
            padding: 8px 5px;
            cursor: pointer;
            color: $header-color;

            > label {
                @include title();
                cursor: pointer;

                display: inline-block;
                width: 90%;
                padding: 5px;
            }
            .marker {
                display: inline-block;
                font-size: 1.4rem;
            }
        }
    }

    .dynamic {  
        cursor: pointer;
        position: absolute;
        right: 20px;
        top: 2px;
        :global(.v-icon){
            font-size: 12pt;
        }
    }
    

    .titleSuffix {        
    }

    .additionalInfo {
        font-size: 0.75em;
        .info {
            color: black
        }
        .error {
            color: red
        }
    }

    .open {
    }

    .closed {
        display: none;
    }

    fieldset {
        border: none;
        border-top: ( none !important );
        padding: 10px 0px;
        margin-top: 10px;
        legend {

        }
        .fieldSetWrapper {          
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

        .dynamic {           
            position: inherit;
            float: right;
            background: white;           
            transform: translateY( 0 ) !important ;
        }
        

        .headingLine {
            flex-grow: 1;
            height: 1px;
            background-color: silver;
            padding: 0 2px;
        }
        
    }

    ul {
        list-style: none;
        padding: 5px !important;
        li {
        }
    }

    .flex {
        display: flex;
        &.columns2 {
            > ul {
                width: 50%;
                :global([class^=property]) > label {
                    width: 90px;
                }
            }
        }
        &.columns3 {
            > ul {
                width: 33%;
                :global([class^=property]) > label {
                    width: 90px;
                }
            }
        }

        ul {
            li {
                width: 100%;
            }
        }
    }
}

.error {
    $width: 20px;
    color: $error-color;
}

.different {
    $width: 20px;
    color: $changed-color;
}
