.waterfall {
    > div {
        height: calc( 100% - 95px );
        width: calc( 100% - 16px );

        canvas {
            height: calc( 100% - 20px );
            width: 100%;
        }

        position: absolute;
    }
    :global(#scaleRangeHigh) {
        margin-top: -110px;
    }
    :global(#scaleRangeLow) {
        margin-top: -110px;
    }
    :global(.scaleRange) {
        left: 0px !important;
        background: transparent;
    }
}