.resolver {
    ul {
        list-style: none;
        padding: 0px;
    }

    label {
        font-weight: bold;
        display: inline-block;
        width: 120px;
    }

    .response {
        height: calc( 100% - 10px );
        display: flex;
        flex-direction: column;

        .comment {
            flex-grow: 9;
            textarea {
                width: 100%;
                margin-left: auto;
                margin-right: auto;
                margin-top: 5px;
                height: 100%;
            }
        }
    }



}