@import '../../Theme/Colors';

.alerts {
    ul {
        list-style: none;
        padding: 0px;
        li {
            border-bottom: $element-border;
            padding: 5px;
            cursor: pointer;

            &:global(.threat_red) {
                color: $error-color;
            }

            &:global(.threat_amber) {
                color: $warning-color;
            }

            &:global(.threat_green) {
                color: $success-color;
            }

            &:global(.threat_clear) {
                color: $info-color;
            }
        }
    }
}