@use '../../../Theme/Colors' as colours;

.soundfield {
    :global( .ft-panel ){
        padding: 1px;
    }

    main {
        height: calc( 100% - 5px ) !important;
        padding: 0px !important;
        padding-top: 2px !important;
        > main {
            width: 100%;
            height: calc( 100% - 10px);
            > section {
                width: 100%;
                height: 100%;
                > div {
                    width: 100%;
                    height: 100%;
                    position: relative !important;
                    > canvas {
                        width: 100% !important;
                        height: 100% !important;
                        top: 0px !important;
                    }
                    :global(.coreToolbarAutohide) {
                        top: -14px !important;
                        transform: translate( -50%, 0 );
                        text-align: center;
                        height: 10px;

                        :global(.maintoolbar){
                            position: relative;
                        }
                        :global(.container){
                            top: -30px;
                            transform: translate( 0px, -100% );
                            transition: transform 300ms ease-in-out, top 300ms ease-in-out;
                            transition-delay: 1s;
                            opacity: 1.0;

                        }
                        &:hover {
                            :global(.container) {
                                top: 0px;
                                transform: translate( 0px, 0px );
                                opacity: 1.0;
                            }
                        }
                    }
                    :global(.scaleRange){
                        position: fixed;
                        margin-left: 30px;
                        font-size: 11px;
                        &:global(#scaleRangeLow){

                        }
                        &:global(#scaleRangeHigh){
                        }
                    }
                }
            }
        }

        .mouseover {
            list-style: none;
            padding: 0px;
            label {
                display: inline-block;
                width: 50px;
            }
        }
    }

    .unstacked {
        :global( .coreToolbarAutohide ){
            :global( .container ){
                width: auto;
                > * {
                    display: inline-block;
                    vertical-align: middle;
                }
            }
        }
        :global( .displayTypeSelector ),
        :global( .decimationSelector ){
            display: inline-block;
            vertical-align: middle;

            > nav > div {
                padding-top: 2px;
            }
        } 
    }

    footer {
        display: none;
    }
}