@import '../../../Theme/Colors';

.group {    
    .entries {        
        padding: 0px;
       @for $i from 1 to 10 {
           li:first-child:nth-last-child(#{$i}),
           li:first-child:nth-last-child(#{$i}) ~ li {              
               width: calc( (100% / #{$i} ) - 10px ) !important;
               display: inline-block;
               white-space: nowrap;              
           }
       }
        &.list {
            li {                               
                display: ( flex !important );                
                width: ( 100% !important );                              
            }
        }
    }

    .title {
        white-space: nowrap;
    }

    .errors {
        color: $error-color;
    }    
}


:global(.group) {
    > [class*=changed] {
        [class*=value] {
            :global(.changed),
            :global(.errors),
            :global(.updated) {
                //display: none;
            }

            :global(.component){
                width: 100%;
            }

            :global(.notes){
                [class^=error]{
                    display: none;
                }
            }

        }
        .nochanges {
            [class*=value] {
                :global(.changed),
                :global(.errors),
                :global(.updated) {
                    display: none;
                }

                :global(.notes){
                    display: none;
                }
            }
        }
    }

    > :global(.notes) {
        display: none;
    }
}