// Branding colours

$fotech-darkgrey: #4a4a49;
$fotech-lightgrey: #878787;
$fotech-green: #009640;
$fotech-lightgreen: #86bc25;
$fotech-yellow: #ffda00;

// Error messages
$error-color: #ff3860;
$error-border-color: #ff3860;
$error-border: 1px solid $error-border-color;
$error-background: #f8d7da;

// Warning messages
$warning-color: #ff8000;
$warning-border-color: #cc6600;
$warning-border: 1px solid $warning-border-color;
$warning-background: #fff3cd;

// Information
$info-color: #0099ff;
$info-border-color: #0099ff;
$info-border: 1px solid $info-border-color;
$info-background: #cce5ff;

// Success
$success-color: #155724;
$success-border-color: #c3e6cb;
$success-border: 1px solid $success-border-color;
$success-background: #d4edda;

// Selections
$selected-color: #004080;
$selected-border-color: #1a8cff;
$selected-border: 1px solid $selected-border-color;
$selected-background: #99ccff;

// Highlights
$highlight-color: #004080;
$highlight-border-color: #80bfff;
$highlight-border: 1px solid $selected-border-color;
$highlight-background: #c3e1ff;

// Active elements
$active-color: #0066cc;

// Changes and Warnings
$changed-color: $warning-color;
$changed-border: $warning-border;

$updated-color: $info-color;
$updated-border: $info-border;

$danger-color: $error-color;
$danger-border: $error-border;

// Common components

// Elements
$element-border-color: #c0c0c0;
$element-color: black;
$element-border: 1px solid $element-border-color;
$element-shadow: inset 0 1px 2px hsla(0,0%,4%,.1);
$element-background: white;
$element-radius: 5px;
$element-padding: 3px;

// Menus and other popups ...
$menu-background: white;
$text-color: black;

// Other items
$label-color: #363636;
$note-color: #505050;

$header-color: #007bff;

@mixin title() {
    font-weight: 300;
    font-size: 1.25rem;
    color: $header-color;
}

$border-color: #c0c0c0;

$labelwidth: 187px;
$labelwidth-wide: 300px;

// Main navigation menus and so on
$panelmanager-background: #181818;
$panelmanager-color: #909090;
$panelmanager-border: #101010;

// UI Mixins and other useful functions

@mixin customScroll() {
    &::-webkit-scrollbar {
        width: 5px;
    }

    &::-webkit-scrollbar-track {
        color: #ddd;
    }

    &::-webkit-scrollbar-thumb {
        background: #666;
    }
}