@import '../../Theme/Colors';

.audio {
    div.framing {
        height: calc( 100% - 10px );
        iframe {
            border: none;
            padding: 0px;
            margin: 0px;
            width: 100%;
            height: 100%;
        }

        .spinner {
        }

        .loaded {
            display: none;
        }

        .error {
            display: none;
            padding: 40px;
            margin-left: auto;
            margin-right: auto;
            color: $error-color;
            text-align: center;
            overflow: hidden;
            border: $error-border;
            border-radius: 5px;
            background: white;
            box-shadow: 0px 0px 10px 0px #808080;
            transition: opacity 3s linear, height 800ms;

            &.view {
                font-size: 14pt;
                display: block;
                color: red;
                div {
                    display: inline-block;
                }
            }
        }
    }

    .hidden {
        visibility: hidden;
    }
}