@use '../../../Theme/Colors' as colors;

.boolean {
    display: inline-block;
    .container {
        $size: 12px;
        height: $size;
        width: $size * 2;
        border: 1px solid #c0c0c0;
        border-radius: $size;
        background-color: #c0c0c0;
        transition: background-color 500ms ease-in-out;
        cursor: pointer;
        box-sizing: initial;

        &.on {
            background-color: #23d160;
            .knob {
                margin-left: $size;
            }
        }

        .knob {
            transition: margin-left 200ms ease-in-out;
            height: $size - 2px;
            width: $size - 2px;
            background-color: white;

            position: relative;
            margin-top: 1px;
            border-radius: 50%;
        }
    }
}

:global(.boolean){
    :global(.notes){
        [class^='changed'] {
            transform: translate( 100%, -100% ) !important;
            &:before {
                border-right: 5px solid colors.$warning-color !important;
                border-top: 2.5px solid transparent !important;
                border-bottom: 2.5px solid transparent !important;
                border-left: 0px !important;
                left: calc( -5px ) !important;
                top: calc( 50% - 2.5px ) !important;
            }
            &:updated {
                border-right: 5px solid colors.$updated-border !important;
                border-top: 2.5px solid transparent !important;
                border-bottom: 2.5px solid transparent !important;
                border-left: 0px !important;
                left: calc( -5px ) !important;
                top: calc( 50% - 2.5px ) !important;
            }
        }
    }
}

:global(.dark) {
    .boolean {
        .container {
            background-color: #606060;
            border-color: #404040;
            &.on {
                background-color: #34a05a;
            }
        }
    }
}

$noEventClasses: 'disabled', 'readonly';
 
@each $class in $noEventClasses {
    [class*=#{$class}] {
        .boolean {
            pointer-events: none;
        }
    }
}
