@import '../../Theme/Colors';

.debug {
    a {
        text-decoration: none;
    }

    main {
        display: flex;
        flex-direction: column;
        height: 100%;
    }

    ul {
        list-style: none;
        li {
            width: 100%;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
        }
    }

    .toolbox {
        border-bottom: $element-border;
        padding: 10px;
        margin: 10px 0px;

    }

    .messages {
        overflow: auto;
        padding: 10px;
        div {
            display: inline;

            &.icon {
                width: 20px;
                display: inline-block;
            }

            &.level {
                width: 55px;
                display: inline-block;
            }

            &.time {
                color: #202020;
            }

            &.message {
                color: #202020;

                .param {
                    color: #078561;
                }

                .null {
                    color: #9403fc;
                }
            }
        }

        pre {
            margin: 0px !important;
            max-height: 120px;
            overflow: auto;
        }

        .error {
            color: $error-color;
        }
        .warn {
            color: $warning-color;
        }
        .info {
            color: $info-color;
            font-style: normal;
        }
        .debug {
            color: $warning-color;
        }

    }
}