@import '../../Theme/Colors';

.logging {
    > label {
        @include title();
    }

    header {
        ul {
            margin-top: 6px;
        }
    }

    .disks {
        font-size: 8pt;
        [class*=meter_] div {
            margin-left: 0px !important;
        }
        margin-bottom: 10px;
    }

    ul {
        list-style: none;
        padding: 0px;
        label {
            display: inline-block;
            width: 90px !important;
        }
    }
}