@use '../../Theme/Colors' as colours;
@use '../../Theme/Utilities' as utils;

.status {
    $status-details-color: #202020;
    display: inline-block;
    
    :global(.v-icon){
        font-size: 1.2em;
        color: inherit;
    }

    &, :global(.v-icon){
        &.off {
            color: #f0f0f0;
        }
        &.ok {
            color: #47d147;
        }
        &.info {
            color: colours.$info-color;
        }
        &.warning {
            color: colours.$warning-color;
        }
        &.error {
            color: colours.$error-color;
        }
        &.critical {
            color: colours.$error-color;
        }
    }
}