@import '../../../Theme/Colors';

.item {
    margin: 3px;
    padding: 5px;
    border: $element-border;
    min-width: 250px;
    min-height: 50px;
    width: 100%;

    :global(.error) {
        margin: 2px;
        background-image: inherit !important;
        background-size: inherit !important;
        background-repeat: inherit !important;
    }
}