@import '../../Theme/Colors';

.panelmanager {
    height: calc( 100vh - 18px );
    background: $panelmanager-background;
    border-right: 1px solid $panelmanager-border;
    width: 165px;
    display: flex;
    flex-direction: column;
    color: $panelmanager-color;

    transition: width 50ms ease-in-out;

    &.closed {
        width: 0px;
        /* Prevent the panel from being clickable when not visible */
        ul {
            pointer-events: none;
            li {
                pointer-events: auto;
            }
        }
    }

    > header {
        .logo {
            width: 150px;
            height: 40px;
        }
        color: $panelmanager-color;
        font-size: 1.4rem;
        margin: 5px;
        white-space: nowrap;
        overflow: hidden;
    }

    > ul {
        overflow: auto;
        overflow-x: hidden;
        @include customScroll();

        > li {
            > section {
                > [class^=panel_] {
                    > [class^=title_] {
                        overflow: hidden;
                        white-space: nowrap;
                        > header > label,
                        > label {
                            font-size: 0.85rem !important;
                            color: $panelmanager-color;
                            padding: 5px 5px;
                            width: calc( 100% - 40px );
                        }
                        [class^=marker_] {
                            width: 20px;
                        }
                    }
                }
            }
        }
        list-style: none;
        padding: 0px;
    }
}