@import '../../../Theme/Colors';

.section {
    margin: 3px;
    padding: 5px;
    border: $element-border;
    border-color: $info-color;
    min-width: 100px;
    min-height: 50px;
    width: 100%;
    box-sizing: border-box;
    overflow: auto;

    main {
        display: flex;
    }

    .add {
        margin: 3px;
        border: 1px dashed $element-border-color;
        text-align: center;
        color: #808080;
        min-width: 150px;
        .button {
            font-size: 30pt;
        }
    }

    .controls {
        width: 100%;
        display: flex;
        margin-left: auto;
        margin-right: auto;
        padding: 2px;
        .vertical, .horizontal {
            margin: 5px;
            div {
                border: $element-border;
            }
        }

        .vertical {
            display: flex;
            flex-direction: column;
            div {
                width: 20px;
                height: 10px;
            }
        }

        .horizontal {
            display: flex;
            div {
                width: 10px;
                height: 18px;
            }
        }

    }
}