@use '../../Theme/Colors' as colours;

.questions {
    .subtitle {
        -webkit-line-clamp: inherit;
    } 
}

.notifications {
    position: fixed;
    display: flex;
    bottom: 90px;
    right: 30px;
    height: 100vh;
    flex-direction: column-reverse;
    pointer-events: none;

    .notification {
        z-index: 999;
        pointer-events: all;
        $delay: 500ms;
        $intermessage: 500ms;
        display: flex;
        justify-content: space-between;

        max-height: 100px;
        transition: max-height $delay ease-in,
                    padding $delay ease-in,
                    background-color $intermessage ease-in-out,
                    border-color $intermessage ease-in-out,
                    color $intermessage ease-in-out;

        width: 320px;
        background-color: white;
        border: colours.$element-border;
        border-radius: 5px;

        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
        margin: 5px;

        padding: 5px;

        box-sizing: border-box;

        > * {
            display: inline-block;
            vertical-align: top;
            &.icon {
                margin: 5px;
            }
        }

        > div {
            flex-grow: 9;
        }

        .close {
            float: right;
        }

        &.hidden {
            max-height: 0px;
            padding: 0px 5px;
        }

        &.ok {
            color: #155724;
            background-color: #d4edda;
            border-color: #c3e6cb;
        }

        &.warning {
            color: #e67300;
            background-color: #fff3cd;
            border-color: #ffeeba;
        }

        &.error {
            color: #721c24;
            background-color: #f8d7da;
            border-color: #f5c6cb;
        }

        &.info {
            color: #0c5460;
            background-color: #d1ecf1;
            border-color: #bee5eb;
        }
    }
}