@use '../../Theme/Colors' as colors;

.simulator {
    .details {
        display: flex;
        flex-direction: column;
        height: calc( 100% - 10px );
        :global( .v-card ) {
            overflow: auto;
            flex-grow: 9;
        }
    }

    .info {
        ul {
            list-style: none;
            display: flex;
            li {
                margin-left: 5px;
                label {
                    display: inline-block;
                    font-weight: bold;
                    margin-right: 4px;
                }
            }
        }
    }

    .item {
        &.error {
            color: colors.$error-color;
        }
        .filedetails {
            display: flex;
            color: #606060;
            font-size: 8px;
            ul {
                list-style: none;
                display: flex;
                li {
                    margin-left: 5px;
                    label {
                        display: inline-block;
                        font-weight: bold;
                        margin-right: 4px;
                    }
                }
            }
        }
    }

    :global( .v-treeview--dense .v-treeview-node__root ) {
        min-height: 25px;
    }
}