:global(#app.panelled) {
    font: 13px arial, helvetica, clean, sans-serif;
    font-size: small;

    position: initial;

    [class^=panel_] {
        :global(.panel) {
            top: 0px !important;
            left: 0px !important;
            z-index: 0 !important;

            width: calc(100% - 2px) !important;
            height: calc(100% - 4px) !important;

            [class^=controls_] {
                :global(.close) {
                    display: none !important;
                }

                :global(.tearoff) {
                    display: none !important;
                }

                :global(.floating) {
                    display: inherit !important;
                }
            }

            >main {
                height: calc(100% - 90px) !important;
                max-height: calc(100% - 90px) !important;

                &[class^=nofooter] {
                    height: calc( 100% - 5px ) !important;
                    max-height: calc( 100% - 5px ) !important;
                }

                [class^=property] {
                    >section {}
                }
            }
        }
    }
}

:global(#vueUUID) {
    display: none;
    position: absolute;
    bottom: 100px;
    width: 260px;
    background: white;
}

:global(.panoptes) :global(#app) {
	width: 0px;
}

/* Override to hide the old menu */
body:not(:global(.panoptes)) {
    :global(#mymenubar) {
        display: none !important;
    }
}
