.servicetoggle {
    display: flex;


    &.dark {
        color: #f0f0f0;
    }

    .caption {
        padding-top: 12px;
        .title {
            font-size: 15px;
            white-space: nowrap;
            display: flex;
        }

        .subtitle {
            color: #a0a0a0;
        }

        :global( .v-icon ){
            color: #f0f0f0;
        }
    }

    &.disabled {
        .title,
        .subtitle,
        :global( .v-icon ) {
            color: #505050;
        }
    }
}