@import '../../Theme/Colors';

.fibrelinecloner {
    display: flex;
    flex-direction: column;

    .error {
        color: $error-color;
    }

    ul {
        list-style: none;

        ul {
            list-style: none;
        }

        .controls {
            text-align: right;
        }
    }

    .controls {
        ul {
            padding: 0px;
            padding-bottom: 10px;
            border-bottom: $element-border;
        }
    }

    .progress {
        text-align: center;
    }

    .log {
        height: 100px;
        overflow: auto;
        margin-top: 20px;
        border-top: 1px solid $element-border;

        .error {
            color: $error-color;
        }

        .info {
            color: $info-color;
        }
    }
   
    .querying {
    	text-align: left;
    }
   
	.loader {
		border: 8px solid #f3f3f3;
		border-radius: 50%;
		border-top: 8px solid #3498db;
		width: 15px;
		height: 15px;
		-webkit-animation: spin 1s linear infinite; /* Safari */
		animation: spin 1s linear infinite;
	}

	/* Safari */
	@-webkit-keyframes spin {
		0% { -webkit-transform: rotate(0deg); }
		100% { -webkit-transform: rotate(360deg); }
	}

	@keyframes spin {
		0% { transform: rotate(0deg); }
		100% { transform: rotate(360deg); }
	}
   
}