@use '../../../Theme/Colors' as colours;

.questions {
    .subtitle {
        -webkit-line-clamp: inherit;
    } 

    &.warning {
        :global(.v-icon){ 
            color: colours.$warning-color;
        }
    }

    &.error {
        :global(.v-icon){ 
            color: colours.$error-color;
        }
    }

    &.info {
        :global(.v-icon){ 
            color: colours.$info-color;
        }
    }

}

.compound {
    max-height: 50vh;
    overflow: auto;
    .message {        
        white-space: normal;
    }
}