
@mixin attentionGrabber {
    &:before{
        $size: 0px;
        width: $size;
        height: $size;
        border: 3px solid red;
        opacity: 0;
        content: " ";
        animation: attention 1s 5;
        position: absolute;
        margin-left: 0px;
        margin-top: 0px;
        border-radius: 50%;
    }
}

@mixin attention {
    @keyframes attention {
        0% {
            width: 18px;
            height: 18px;
            margin-left: -2px;
            margin-top: -2px;
            opacity: 0.1;
            border-width: 1px;
        }
        50% {
            width: 30px;
            height: 30px;
            margin-left: -8px;
            margin-top: -8px;
            opacity: 0.5;
            border-width: 2px;
        }
        100% {
            width: 60px;
            height: 60px;
            margin-left: -22px;
            margin-top: -22px;
            opacity: 0.1;
            border-width: 1px;
        }
    }
}